let numberclock = document.querySelector('.numberclock');
let hourHand = document.querySelector('.hour-hand');
let secondHand = document.querySelector('.second-hand');
let minuteHand = document.querySelector('.minute-hand');



(function getTime() {
  let dt = new Date();
  let h = dt.getHours();
  let m = dt.getMinutes();
  let s = dt.getSeconds();
  numberclock.innerHTML = `${h} : ${m} : ${s}`;
  let hdeg = 360 / 12 * h - 90;
  hourHand.style.transform = `rotate(${hdeg}deg)`;
  let mdeg = 360 / 60 * m + 180;
  minuteHand.style.transform = `rotate(${mdeg}deg)`;
  let sdeg = 360 / 60 * s + 180;
  secondHand.style.transform = `rotate(${sdeg}deg)`;

  setTimeout(getTime, 1000);
})();